import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function evToOperatingCashFlowTtm(tableName: string) {
  const title = "EV/OCF (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["evToOperatingCashFlowTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "EV vs. operating cash flow.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about enterprise value to operating cash flow or other relevant financial measures?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data?.evToOperatingCashFlowTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.evToOperatingCashFlowTtm}
        />
      )
    },
  })
}

export { evToOperatingCashFlowTtm }
